import React, { useState, useEffect } from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
import NotFound from './NotFound';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name)

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: []});

    // the annonymous function is called everytime the component loads
    // CAVEAT: useEffect is called not only when the component is Mount but also
    // when the component gets updated
    // passing an emptry array will check if the value gets updated, so for the 
    // first time it will load and then next time do not! Hence passing the route name 
    // to keep a check on loading the component when the route change , will help!
    useEffect(() => {
        // setArticleInfo({ upvotes: Math.ceil(Math.random() * 10) })
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log('Body JSON', body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) return <NotFound/>
    const otherArticle = articleContent.filter(article => article.name !== name)
    return (
        <React.Fragment>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name}
                upvotes={articleInfo.upvotes}
                setArticleInfo= {setArticleInfo} />
            {article.content.map((paragraph,key) => (
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles: </h3>
            <ArticlesList articles={otherArticle}/>
        </React.Fragment>
    );
};

export default ArticlePage;